@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400&family=Quicksand:wght@300;400&display=swap);
*,
*::before,
*::after {
  margin: 0;
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

html {
  --ff-primary: "Quicksand", sans-serif;
  --ff-mono: "Fira Code", monospace;

  --fs-h1: 3rem;
  --fs-h2: 2.4rem;
  --fs-h3: 1.95rem;
  --fs-h4: 1.5rem;
  --fs-body: 1.35rem;

  --bg: #fefefe;

  --clr-black: #1e1e1e;
  --clr-dark: #212121;
  --clr-light: #f8f8f8;

  overflow-x: hidden;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
}

@media (min-width: 600px) {
  html {
    --fs-h1: 3.1rem;
    --fs-h2: 2.5rem;
    --fs-h3: 2.1rem;
    --fs-h4: 1.65rem;
    --fs-body: 1.4rem;
  }
}

@media (min-width: 800px) {
  html {
    --fs-h1: 3.5rem;
    --fs-h2: 2.8rem;
    --fs-h3: 2.3rem;
    --fs-h4: 1.75rem;
    --fs-body: 1.5rem;
  }
}

@media (min-width: 1000px) {
  html {
    --fs-h1: 3.65rem;
    --fs-h2: 2.9rem;
    --fs-h3: 2.4rem;
    --fs-h4: 1.85rem;
    --fs-body: 1.6rem;
  }
}

@media (min-width: 1200px) {
  html {
    --fs-h1: 4.25rem;
    --fs-h2: 3.5rem;
    --fs-h3: 2.45rem;
    --fs-h4: 2rem;
    --fs-body: 1.65rem;
  }
}

@media (min-width: 1500px) {
  html {
    --fs-h1: 4.9rem;
    --fs-h2: 3.65rem;
    --fs-h3: 2.55rem;
    --fs-h4: 2.15rem;
    --fs-body: 1.7rem;
  }
}

@media (min-width: 1750px) {
  html {
    --fs-h1: 5.25rem;
    --fs-h2: 3.9rem;
    --fs-h3: 2.75rem;
    --fs-h4: 2.25rem;
    --fs-body: 1.75rem;
  }
}

@media (min-width: 2000px) {
  html {
    --fs-h1: 5.9rem;
    --fs-h2: 4.1rem;
    --fs-h3: 2.95rem;
    --fs-h4: 2.35rem;
    --fs-body: 1.85rem;
  }
}

body {
  margin: 0;
}

h1 {
  font-size: 3rem;
  font-size: var(--fs-h1);
}

h2 {
  font-size: 2.4rem;
  font-size: var(--fs-h2);
}

h3 {
  font-size: 1.95rem;
  font-size: var(--fs-h3);
}

h4 {
  font-size: 1.5rem;
  font-size: var(--fs-h4);
}

a {
  text-decoration: none;
  color: unset;
}

.content {
  min-height: calc(100vh - 250px);
}

.App {
  font-family: "Quicksand", sans-serif;
  font-family: var(--ff-primary);
  font-weight: 300;
  font-size: 1.35rem;
  font-size: var(--fs-body);
  color: #212121;
  color: var(--clr-dark);
  background-color: #fefefe;
  background-color: var(--bg);
  transition: 0.2s color ease, 0.2s background-color ease;
}

@media (min-width: 800px) {
  .content {
    min-height: calc(100vh - 300px);
  }
}

@media (min-width: 1200px) {
  .content {
    min-height: calc(100vh - 150px);
  }
}

/* DARK MODE */

.dark-mode .App {
  color: #f8f8f8;
  color: var(--clr-light);
  background-color: #212121;
  background-color: var(--clr-dark);
}

.dark-mode .header {
  background-color: #212121;
  background-color: var(--clr-dark);
  box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.5);
}

.dark-mode .post-timestamp {
  color: #ccc;
}

.dark-mode .post-author {
  color: #6fffe7;
}

.dark-mode .post-suggestion {
  color: #6fffe7;
}

.dark-mode .post-suggestion::marker {
  color: #111;
}

.dark-mode .light {
  background-color: rgb(42, 42, 42);
  color: #f8f8f8;
  color: var(--clr-light);
}

.dark-mode .light .keyword {
  color: #8aa5ff;
}

.dark-mode .light .class {
  color: #5f87f6;
}

.dark-mode .light .var {
  color: #6fffe7;
}

.dark-mode .light .property {
  color: #84a9ff;
}

.dark-mode .light .string {
  color: #00ff9d;
}

.dark-mode .light .comment {
  color: #aaaaaa;
}

.dark-mode .note {
  border: 1px solid rgba(200, 200, 200, 0.75);
  box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.75);
  background-color: #212121;
  background-color: var(--clr-dark);
}

.dark-mode .note-text:before {
  background: linear-gradient(transparent, #212121);
  background: linear-gradient(transparent, var(--clr-dark));
}

.dark-mode .note:hover {
  transform: translate(-1px, -4px);
  box-shadow: 12px 15px 20px 0px rgba(0, 0, 0, 0.75);
}

.header {
  background-color: white;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100vw;
  max-width: 100vw;
  z-index: 100;
  transition: 0.2s color ease, 0.2s background-color ease, 0.2s box-shadow ease;
}

.logo-container {
  padding: 1rem 1rem 0;
}

.logo {
  font-family: var(--ff-mono);
  font-weight: 300;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 0.25rem;
  font-size: var(--fs-h2);
}

.header-text {
  padding: 0 1.25rem;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.header-text p {
  padding: 0.25rem 1.25rem 1rem;
  font-size: var(--fs-h4);
}

@media (min-width: 600px) {
  .logo-container {
    padding: 1.5rem 1rem 0;
  }

  .header-text {
    padding: 0.25rem 1.25rem;
  }
}

@media (min-width: 800px) {
  .header {
    flex-direction: row;
  }

  .logo-container {
    padding: 1.5rem 2rem;
  }

  .logo {
    font-size: var(--fs-h3);
  }

  .header-text {
    padding: 0.5rem 1rem;
  }

  .header-text p {
    padding: 0.5rem 1.25rem;
    font-size: var(--fs-h4);
  }
}

.footer {
  height: 250px;
  max-height: 250px;
  background: var(--clr-black);
  color: var(--clr-light-grey);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.left {
  padding: 3rem 2rem 0rem;
}

.middle {
  display: flex;
  align-items: center;
}

.middle span {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: none;
}

.right {
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  flex: 1 1;
  width: 500px;
  justify-content: space-around;
}

.latest {
  color: lightskyblue;
  font-size: var(--fs-h4);
  padding: 1rem 2rem;
}

@media (min-width: 600px) {
  .middle span {
    display: inline;
    display: initial;
  }
}

@media (min-width: 800px) {
  .footer {
    height: 300px;
    max-height: 300px;
  }

  .right {
    width: 600px;
  }
}

@media (min-width: 1200px) {
  .footer {
    height: 150px;
    max-height: 150px;
    flex-direction: row;
  }

  .left {
    padding: 1rem 4rem;
  }

  .right {
    flex: 1 1;
  }
}

.dark,
.light {
  font-family: var(--ff-mono);
  font-size: calc(var(--fs-body) - 0.25rem);
  padding: 1rem;
  border-radius: 8px;
  transition: 0.2s color ease, 0.2s background-color ease;
}

.dark {
  background-color: #222;
  color: var(--clr-light);
}

.light {
  background-color: #eee;
  color: var(--clr-dark);
}

.dark span,
.light span {
  transition: 0.2s color ease;
}

.dark .keyword {
  color: #8aa5ff;
}

.dark .class {
  color: #5f87f6;
}

.dark .var {
  color: #6fffe7;
}

.dark .property {
  color: #3359ff;
}

.dark .string {
  color: #00ff9d;
}

.dark .comment {
  color: #cccccc;
}

.light .keyword {
  color: #7340ff;
}

.light .class {
  color: #0044ff;
}

.light .var {
  color: #0077ff;
}

.light .property {
  color: #341dff;
}

.light .string {
  color: #8c00ff;
}

.light .comment {
  color: #444444;
}

.normal {
  color: inherit !important;
}

@media (min-width: 600px) {
  .large {
    font-size: var(--fs-body);
  }
}

.note {
  border: 1px solid rgba(50, 50, 50, 0.75);
  border-radius: 4px;
  margin: 1rem 1rem;
  padding: 0.75rem;
  box-shadow: 7px 7px 5px 0px rgba(50, 50, 50, 0.75);
  min-height: 200px;
  background-color: white;
  transition: 0.3s transform ease-out, 0.3s box-shadow ease-out;
  display: flex;
  flex-direction: column;
}

.note-title {
  margin: 0.25rem 0 0.5rem;
}

.note hr {
  border: 1px solid #666;
  border-radius: 1px;
  margin-bottom: 0.5rem;
}

.note-text {
  position: relative;
  max-width: 90%;
  flex: 1 1;
}

.note-text:before {
  pointer-events: none;
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: linear-gradient(transparent, white);
}

.note:hover {
  transform: translate(-2px, -5px);
  box-shadow: 10px 15px 7px 0px rgba(50, 50, 50, 0.75);
}

.home {
  padding-top: 135px;
  max-width: 95%;
  margin: 0 auto;
}

@media (min-width: 600px) {
  .home {
    padding-top: 155px;
  }
}

@media (min-width: 800px) {
  .home {
    padding-top: 115px;
  }
}

@media (min-width: 1000px) {
  .home {
    padding-top: 125px;
    max-width: 92.5%;
  }
}

@media (min-width: 1200px) {
  .home {
    padding-top: 125px;
  }
}

@media (min-width: 1500px) {
  .home {
    padding-top: 130px;
    max-width: 86%;
  }
}

@media (min-width: 2000px) {
  .home {
    padding-top: 135px;
    max-width: 80%;
  }
}

.home-grid {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-bottom: 1rem;
}

@media (min-width: 800px) {
  .home-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 1200px) {
  .home-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

